<script>
import axios from "axios";

export default {
  name: "MyLoans",
  data() {
    return {
      loans: [],
      error: ''
    }
  },
  methods: {
    getLoans: function () {
      /*let data = {
        partial_name: this.search
      }*/
      axios.get("/spring/author/allAuthors")
          .then((response) => {
            console.log(response.data);
            this.$set(this, 'loans', response.data);
            console.log(this.loans);
          })
          .catch((errors) => {
            console.log(errors);
            this.error = "Une erreur est survenue.";
          })
    }
  },
  mounted() {
    this.getLoans();
  }
}
</script>

<template>
  <div>
    <div class="bg-grey">
      <div class="home-banner bg-red text-white p-5">
        <h3>Mes emprunts ({{ this.loans.length }})</h3>
      </div>

      <div class="p-5" style="min-height: 70vh">
        <div class="alert alert-warning" role="alert" v-if="this.loans.length === 0">
          Vous n'avez aucun emprunt en cours
        </div>
        <div class="alert alert-danger" role="alert" v-if="this.error.length > 0">
          {{ this.error }}
        </div>
        <div v-for="loan in this.loans" :key="loan">
          <div class="bg-dark-grey m-3 p-3">
            <h5>{{ loan._id }}</h5>
            <p>Durée de l'emprunt : {{ 0 }}</p>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

